import {
	createCarDriver  //保存
} from '../../api/person/response.js'
export default {
	data() {		
		return {				
			formModel: {
				fields: {
					SystemUserUuid:"",
					problemContent:"",
					problemType:"",
					remarks:"",
				},
			}
		}
	},
	onLoad() {
		if (this.$store.state.userId == '') {
			uni.showModal({
				title: '提示',
				content: '请在微信授权后查看！',
				showCancel: true,
				success: function(rest) {
					if (rest.confirm) {
						uni.navigateTo({
							url: '/pages/login/login'
						});
					} else {
						uni.navigateBack({
						    delta: 1,
						    animationDuration: 200
						});
					}
				}
			});
		} else {
		this.$checkusertoken();
		}
	},
	methods: {						
		//保存
		submitfrom() {
			let valid = this.validateCarDispatchForm();
			this.formModel.fields.SystemUserUuid=this.$store.state.userId;
			console.log(this.formModel.fields.SystemUserUuid);
			console.log(this.$store.state.userId);
			if (valid != "true") {
				uni.showModal({
					title: '提示',
					content: valid,
					showCancel: false
				})
			} else {
				createCarDriver(this.formModel.fields).then(res => {
					if (res.data.code == 200) {
						uni.showModal({
							title: '提示',
							content:'反馈'+ res.data.message,
							showCancel: false,
							success: (r) => {
								if (r.confirm) {
									uni.redirectTo({
										url: '/pages/home/index'
									})
								}
							}
						})
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.message,
							showCancel: false
						})
					}
				})
			}

		},
		//非空验证
		validateCarDispatchForm() {
			let _valid = "true";
			// if (this.formModel.fields.realName == '' || this.formModel.fields.realName == null) {
			// 	_valid = "用户姓名不能为空";
			// 	return _valid;
			// }
			if (this.formModel.fields.problemType == '' || this.formModel.fields.problemType == null) {
				_valid = "问题类型不能为空";
				return _valid;
			}
			if (this.formModel.fields.problemContent == '' || this.formModel.fields.problemContent == null) {
			   _valid = "反馈内容不能为空";
			  return _valid;
		  }	
			return _valid;
		}
	}
}
